body {
  background-image: url("images/0okk091z2z.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  color: white;
}

body.g {
  background-image: url("images/59619mpdi0.png");
}

body.fullscreen {
  background-image: url("images/rw6e3f3011.jpg");
}

#support-text {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 24px;
  text-align: center;
  width: 100%;
  font-weight: bold;
  color: black;
  display: none;
}

#support-text span {
  font-weight: normal;
}

#music {
  display: none;
}

#modal-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

#modal-content {
  background-color: white;
  border-radius: 5px;
  max-width: 400px;
  margin: 100px auto;
  padding: 20px;
  text-align: center;
}

#modal-close {
  cursor: pointer;
  color: #888;
  float: right;
  font-size: 20px;
  font-weight: bold;
}

#modal-message {
  color: black;
  font-weight: bold;
  margin-bottom: 20px;
}

#modal-image {
  margin-bottom: 20px;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.modal-buttons button {
  width: 100px;
  padding: 10px 0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
}

.modal-buttons button.accept {
  background-color: white;
  color: black;
}

.modal-buttons button.dismiss {
  background-color: #3b5998;
  color: white;
}

.modal-buttons button:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

@keyframes blink {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.modal-buttons button.dismiss.blinking {
  animation: blink 1s infinite;
}

#welcomeDiv {
  display: none;
  background-color: #000;
  height: auto;
  width: 550px;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 9999999999;
  text-align: center;
}

#welcomeDiv p {
  color: #fefefe;
  margin-top: 10px;
  font-size: 16px;
  opacity: 0.9;
}
